
.courseCard{
    padding: 50px 0;
}

.courseCard .items{
    background-color: #fff; 
    padding: 30px;
    text-align: center;
    

}

.courseCard .image{
    width: 80px;
    height: 80px;
    border-radius: 50%;
    background-color: #1eb2a6;
    padding: 15px;

}

.courseCard img{
    width: 100%;
    height: 100%;
    object-fit: cover;
}

.courseCard .text{

    margin-left: 15px;
    text-align: left;
    
    
  

}

.courseCard .text h1{
    font-size: 25px;
    font-weight: 500;
    line-height: 45px;
    

}

.courseCard .rate{
   margin: 20px 0;
   color: #1eb2a6 ;

}

.courseCard .rate .rateIcon{
    font-size: 13px;
    margin-right: 5px;

}

.courseCard .details .dimg img {
  
   width: 50px;
   height: 50px;
   border-radius: 50%;
   margin-top: 20px;
}

.courseCard .details .box{
    display: flex;
    align-items: center;
    color: grey;

}

.courseCard .details span{
   color: #1eb2a6;
   font-weight: 500;
   font-size: 14px;
}


.courseCard .price{
    margin: 30px 0;
    background-color: #f8f8f8;
    padding: 10px;
}


.courseCard h3{
    font-weight: 500;
    color: #1eb2a6;
}

.online{
    text-align: center;
}


.online .box{
    box-shadow: 0 5px 25px -2px rgba(0 0 0 /6%);
    background-color: #fff;
    padding: 30px 20px ; 
    transition: 0.5s;
}

.online .image{
    width: 80px;
    height: 80px;
    margin: auto;
    position: relative;
}
.online img{
    width: 100%;
    height: 100%;
    object-fit: cover;
}

.online h1{
    font-weight: 500;
    font-size: 20px;
    margin: 20px 0;
    line-height: 30px;

}

.online span{
    background-color: #f8f8f8;
    padding: 5px 20px;
    font-weight: 500;
    font-size: 15px;
    color: #1eb2a6;
    border-radius: 5px;
    
}

.online .box .show{
    opacity: 0;
}

.online .box:hover{
    background-color: #1eb2a6;
    border-radius: 5px;
    cursor: pointer;
}

.online .box:hover .show{
    opacity: 1;
    position: absolute;
    top: 0;
    left: 0;
     


}

.online .box:hover h1{
    color: #fff;
}


@media screen and (max-width: 1115px) {
    .gridz{
        margin-top: 5%;
        margin-bottom: 5%;
    }
}


 


